<template>
<div class="fx-healthy">
    <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text>
    <div class="fx-content" :style="{height:clientHeight1+'px'}">
        <div class="fx-space"></div>
        <div class="fx-tab">
            <div class="text text1">投保人健康告知</div>
            <div class="text text1">被保险人健康告知</div>
        </div>
        <div v-html="holderHealthContent" :style="{height:clientHeight+'px',overflow:'auto'}"></div>
    </div>
    <div class="fx-btns">
        <div class="nextBtn" @click="goNext()">下一步</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
export default {
    name:'healthy1',
    data(){
        return{
            clientHeight:0,
            clientHeight1:0,
            headerTitle:'健康告知',
            holderHealthContent:"被保险人健康告知",//投保人健康告知
            insureHealthContent:"",//被保险人健康告知
        }
    },
    created() {

    },
    methods: {
        //进入智能审核
        goNext(){
            //弹出一个弹窗 订单填写
            //this.$router.push({path:'/fuxing/form'})
            // console.log('aaa');
        },
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //请求健康告知
        getHealthContent(){
            
        }
    },
    computed:{},
    components:{
        "header-text":headerText
    },
    mounted(){
        this.getHealthContent();
        console.log(window.innerHeight);
        this.clientHeight1 = window.innerHeight - document.getElementsByClassName("fx-header")[0].clientHeight;
        this.clientHeight = this.clientHeight1-document.getElementsByClassName("fx-btns")[0].clientHeight-document.getElementsByClassName("fx-tab")[0].clientHeight-document.getElementsByClassName("fx-space")[0].clientHeight;
    }
}
</script>

<style lang="scss">
.fx-healthy{
    width:100%;
    height:100%;
    background:#fff;
    position: relative;
    overflow: hidden;
    .fx-content{
        overflow: hidden;
        .fx-space{
            height:r(16);
            width:100%;
        }
        .fx-tab{
            background: url("../../../static/images/tab2.png") no-repeat;
            height:r(134);
            width:r(750);
            background-size:100% r(75);
            margin-top:r(16);
            padding-top:r(75);
            .text{
                font-size: r(28);
                width: 50%;
                float: left;
                text-align: center;
                height:r(59);
                line-height:r(59);
                color:#000;
            }
            .text1{
                color:#0084ff;
            }
        }
    }
    .fx-btns{
        height:r(92);
        position:absolute;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:100%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
    }
}
</style>


